@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.2/css/all.min.css);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;600;900&family=Poppins:ital,wght@0,100;0,300;0,400;0,500;0,900;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* font-family: 'Poppins', sans-serif; */

/* font-family: 'Montserrat', sans-serif;
font-family: 'Poppins', sans-serif; */




.App{ 
  height: 100vh;
  width: 100vw;
  background-color: rgb(0, 0, 0);
}

.wrapper{
  display: flex;
  position: absolute;
  width: 85%;
  justify-content: center;
  align-items: center;
  top:50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.side-nav{
  height:485px;
  flex:10% 1;
  background-color: #313131e6;
  display: flex;
  flex-direction: column;
}

.profile-pic{
  height:550px;
  letter-spacing: 2px;
  flex:40% 1;
  background-color: #3c3c3ce6;
  position: relative;
}

.content{
  height:485px;
  flex: 50% 1;
  background-color: #313131e6;
  display: flex;
  justify-content: center;
}

.side-nav .icons{
    color: rgb(210, 225, 238);
    font-size: 4vmin;
    margin: auto;
}


.side-nav .icons .fas{
    cursor: pointer;
    text-shadow: 0 0 3px #00c483;
}

.side-nav .icons :hover{
    color: #313131e6;
}

.content div h1{
    color: #f2edd7;
    font-family: Poppins;
}


.about-me{
    display: flex;
    flex-direction: column;
    align-items: center;
    letter-spacing: 2px;
}

.about-me div{
    font-family: Roboto;
}

.about-info{
    color: #6cb963d4;
    font-size: 1.1rem;
    font-style: italic;
    font-weight: bold;
    letter-spacing: 2px;
    margin-top: -20px;
}

.about-description{
    text-align: justify;
    width: 90%;
    color: #b2bec3;
    margin-top: 12px;
}

.about-services{
    display: flex;
    flex-direction: column;
    width: 90%;
}

.service-header>h2{
    color: #f2edd7;
    font-family: Poppins;
}

.service-info{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}
.service-info div{
    flex: 50% 1;
    color: aliceblue;
}

.service-info div i{
    color: #488a418c;
    font-size: 2.5rem;
}


.academic{
    display: flex;
    flex-direction: column;
    align-items: center;
    letter-spacing: 2px;
    width: 90%;
}

 .line{
    width: 100%;
    height: 1px;
    background-color: #5a8a874d;
    margin-top: -16px;
}

.institutes{
    margin-top: 12px;
    display: flex;
    flex-wrap: wrap;
    color: aliceblue;
    width: 100%;
    overflow: auto;
}

.institutes .line-below {
    margin: 12px 0px;
    background: rgba(144, 238, 121, 0.707);
    border-radius: 12px;
}

.institutes .date{
    width: 40%;
    font-size: 12px;
    font-weight: bold;
}

.institute{
    width: 55%;
    margin-left: 12px;
}

.institute blockquote{
    margin:0px;
    padding:0px;
}

.academic ::-webkit-scrollbar {
    position: absolute;
    left: 222px;
    width: 4px;
  }
  
  /* Track */
  .academic ::-webkit-scrollbar-track {
    background: #f1f1f1; 
  }
   
  /* Handle */
  .academic ::-webkit-scrollbar-thumb {
    background: #888; 
    border-radius: 66px;
  }
  
  /* Handle on hover */
  .academic ::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }
.my-skills{
    display: flex;
    flex-direction: column;
    align-items: center;
    letter-spacing: 2px;
    width: 90%;
}

.my-skills .skills{
    margin-top: 12px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    color: aliceblue;
    width: 100%;
    margin-top: 30px;
}

.skills .progress{
    width: 95%;
    display: flex;
    flex-direction: column;
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
    background-color: rgb(73 101 82 / 27%);
    border-radius: 12px;

}

.progress-bar{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: #e2dbdb;
    text-align: center;
    border-radius: 12px;
    color: black;
    font-size: 0.8em;

}




.projects{
    display: flex;
    flex-direction: column;
    align-items: center;
    letter-spacing: 2px;
    width: 90%;
}
.picture{
    width: 90%;
    height: 90%;
    border-radius: 50%;
    position: absolute;
    top: 5%;
    left: 5%;
    margin: auto;
}

.picture-container {
    background: #363636;
    width: 200px;
    height:200px;
    position: absolute;
    top: 20%;
    left:25%;
    border-radius: 50%;
}


.my-info{
    text-align: center;
    color: #7eac79d9;
    position: absolute;
    top: 55%;
    left: 21%;
}

.cover-pic{
    width:440px;
    height: 200px;
    background-repeat: no-repeat!important;
  background-size: 440px 200px!important; 
}


.cover-pic img{
   width: 100%;
        height: 100%;
        display: block;
        background-size: cover;
}
